import { createStore, combineReducers } from "redux"
import { loadState, persistState } from './localStorage.js';
import undoable, { ActionCreators }  from "redux-undo";


let initHistory = {
  past:  [{
    r1: [],
    r2: [],
    r3: [],
    r4: [],
    r5: []
  }],
  present: { 
    r1: [],
    r2: [],
    r3: [],
    r4: [],
    r5: []
  },
  future: 
     [{
      r1: [],
      r2: [],
      r3: [],
      r4: [],
      r5: []
    }]  
}

function reducer(state = initHistory.present, action) {
  switch (action.type) {
    case 'ADD_CATEGORY': {
      let {reward, addedCategory} = action.payload;
      return {
        ...state,
        [reward]: [...state[reward], addedCategory]
      }
    }
    case 'REMOVE_CATEGORY': {
      let {reward, removedCategory} = action.payload;
      let updatedCategories = state[reward].filter((cat) => cat !== removedCategory);
      return {
        ...state,
        [reward]: updatedCategories
      }
    }
    default:
      return state;
  }
}

const persistedState = localStorage.getItem('boardState') 
                       ? loadState()
                       : initHistory

let store = createStore(undoable(reducer), persistedState);

store.subscribe(()=>{
  persistState(store.getState());
});

combineReducers({
  reducer: undoable(reducer)
})

const draggableTile = document.getElementById('drag-tile');

function dragElement (el) {
  let {pos1, pos2, pos3, pos4} = 0;
  document.onmousedown = elementDrag;

  function elementDrag (e) {
    e.preventDefault();
    if (e.target.classList.contains("parent-tile")) {
      pos3 = e.clientX;
      pos4 = e.clientY;
  
      el.style.opacity = "1";

      el.style.top = e.target.offsetTop + "px";
      el.style.left = e.target.offsetLeft + "px";
      
      document.onmouseup = closeDrag;
      document.onmousemove = dragEl;
    }

  }

  function dragEl (e) {
    // calculate the new cursor position:
    pos1 = pos3 - e.clientX;
    pos2 = pos4 - e.clientY;
    pos3 = e.clientX;
    pos4 = e.clientY;
    // set the element's new position:
    el.style.top = (el.offsetTop - pos2) + "px";
    el.style.left = (el.offsetLeft - pos1) + "px";
  }

  function closeDrag (e) {
    /* stop moving when mouse button is released:*/
    el.style.opacity = "0";

    const dropTarget = document.elementFromPoint(e.clientX, e.clientY);
    if (dropTarget.className === "category") {
      const innerDropTarget = dropTarget.firstElementChild;
      const reward = innerDropTarget.classList[1];
      const category = innerDropTarget.classList[2];

      store.dispatch({type: "ADD_CATEGORY", payload: {
        reward: reward,
        addedCategory: category
      }})

      document.querySelector(`.remove-btn.${reward}.${category}`).style.opacity = "1";
      innerDropTarget.style.visibility = "visible";
      innerDropTarget.style.opacity = "1";
    }
    document.onmouseup = null;
    document.onmousemove = null;
  }
}

dragElement(draggableTile);

function renderTilesAndButtons(state) {
  let i = 1;
  let j = 1;
  document.querySelectorAll(".category").forEach((category) => {
    if (i > 5) {i = 1};
    if (j > 5) {
      j = 1;
      i++;
    }

    const tile = document.createElement("div");
    const removeBtn = document.createElement("div");
    tile.className = `reward-tile r${i} c${j}`;
    removeBtn.className = `remove-btn r${i} c${j}`;
    removeBtn.innerText = 'x';

    mapTilesToBoardState(i, j, tile, removeBtn, state)

    category.appendChild(tile);
    category.appendChild(removeBtn);

    removeBtn.addEventListener("click", e => {
      const reward = e.target.classList[1];
      const category = e.target.classList[2]
      const tile = document.querySelector(`.${reward}.${category}`);
      tile.style.opacity = "0";
      setTimeout(() => {tile.style.visibility = "hidden"}, 1000);
      e.target.style.opacity = "0";
    
      store.dispatch({type: "REMOVE_CATEGORY", payload: {
        reward: reward,
        removedCategory: category
      }}) 
    })

    j++;
  })
}

function mapTilesToBoardState (i, j, tile, removeBtn, state) {
  if (state.present[`r${i}`].includes(`c${j}`)) {
    tile.style.opacity = "1";
    tile.style.visibility = "visible";
    removeBtn.style.opacity = "1";
  } else {
    tile.style.opacity = "0";
    setTimeout(() => {tile.style.visibility = "hidden"}, 1000);
    removeBtn.style.opacity = "0"; 
  }
}

function updateBoard () {
  const state = store.getState();

  let i = 1;
  let j = 1;
  document.querySelectorAll(".category").forEach((category) => {
    if (i > 5) {i = 1};
    if (j > 5) {
      j = 1;
      i++;
    }
    let tile = document.querySelector(`.reward-tile.r${i}.c${j}`);
    let btn = document.querySelector(`.remove-btn.r${i}.c${j}`);

    mapTilesToBoardState(i, j, tile, btn, state);
    j++;
  })
}

document.querySelector(".undo-btn").addEventListener("click", () => {
  // document.querySelector()
  store.dispatch(ActionCreators.undo())
  updateBoard();
})

document.querySelector(".redo-btn").addEventListener("click", () => {
  store.dispatch(ActionCreators.redo());
  updateBoard();
})


function init () {
  renderTilesAndButtons(persistedState);
}

init();